import ProjectCard from "../components/ProjectCard";
import { Box } from "@mui/material";
import React from "react";

function Projects() {
    const projects = [
        {
            label: "Topics in Undergraduate Mathematics",
            description:
                "(IN PROGRESS) A collection of mathematics notes taken during my undergraduate years. I envision it to grow in length over the years.",
            to: "/projects/undergrad-maths.pdf",
            image: "/images/undergrad-maths.png",
            created: 2023,
        },
        {
            label: "Mathematics Olympiad: A Guidebook",
            description:
                "(UNDER CONSTRUCTION) This book serves as a collection of topics in mathematics olympiad, which might be useful in preparing for mathematics olympiad competitions.",
            to: "",
            image: "",
            created: 2023,
        },
        {
            label: "Different Sizes of Infinity",
            description:
                "This is a poster designed for the Mathematics Week at my junior college. The poster covers the cardinality of sets. The intended audience is high school students.",
            to: "/projects/infinity.pdf",
            image: "",
            created: 2023,
        },
    ];

    return (
        <>
            <h1>Projects</h1>
            <Box
                sx={{
                    display: "grid",
                    gridTemplateColumns: "repeat(auto-fit, minmax(280px, 1fr))",
                    gap: 2,
                    width: "100%",
                }}
            >
                {projects.map((project) => (
                    <ProjectCard
                        label={project.label}
                        description={project.description}
                        to={project.to}
                        image={project.image}
                        key={project.created}
                    />
                ))}
            </Box>
        </>
    );
}

export default Projects;
