import { Link } from "react-router-dom";
import React from "react";
import Typewriter from "typewriter-effect";

const Home: React.FC = () => {
    return (
        <>
            <h1>Ryan Joo&apos;s Portfolio</h1>
            <p>
                <Typewriter
                    options={{
                        loop: true,
                    }}
                    onInit={(typewriter) => {
                        typewriter.typeString("Welcome to my website!").pauseFor(1000).deleteAll().start();
                    }}
                />
            </p>
            <p>
                I am a first-year undergraduate student at the National University of Singapore (NUS), studying
                Mathematics and Computer Science. This is a double degree programme, and I will be graduating with a
                Bachelor of Science (BSc) in Mathematics, as well as a Bachelor of Computing (BComp) in Computer
                Science.
            </p>

            <p>
                {"You can view some of my "}
                <Link to="/projects">{"projects"}</Link>
                {", or refer to some of my "}
                <Link to="/notes">{"notes"}</Link>
                {" that I have taken at school."}
            </p>
        </>
    );
};

export default Home;
